.progress {
  position: absolute;
  z-index: 10;
}
#viewport {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}
#canvas {
  background-color: #f8f8f8;
  display: block;
}
md-divider {
  margin-top: 10px;
}
.md-avatar {
  border-radius: 0;
  vertical-align: middle;
}
.editing .component {
  cursor: move !important;
}
.component.dragging {
  opacity: 0.75;
}
#light-bulb.off .light-bulb-st3 {
  fill: transparent;
}
#light-bulb.off .light-bulb-st4 {
  stroke: #888;
}
#light-bulb.off .light-bulb-st5 {
  display: none;
}
#pushbutton {
  cursor: pointer;
}
#pushbutton.pressed .pushbutton-st2 {
  display: none;
}
